<style lang="scss">
#asnForceWrap{
  width:100%;
  height:1000px;
}
#asnForceChart{
  width:100%;
  height:1000px;
}
</style>
<template>
  <div id="asnForceWrap">
    <div id="asnForceChart">
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import http from '../../../../../assets/js/http';
import echarts from 'echarts/lib/echarts';
export default {
  data () {
    return {
      asnChart: null,
      toolTips: null,
      nodes: [{name: 3661, draggable: true}],
      links: [{source: "3661", target: "ix33"}]
    }
  },
  // computed: {
  //   cnodes: function () { return this.nodes; },
  //   clinks: function () { return this.links; }
  // },
  methods: {
    asnOption (nodes, links) {
      return {
        title: {
          text: ''
        },
        tooltip: {
          "formatter": function (param) {
            console.log(param.data)
            return `
                <b style="color:#26b8f2">UPDATED</b> : ${param.data.toolTip.updated}</br>
                <b style="color:#26b8f2">OWNER_ADDRESS</b> : ${param.data.toolTip.owner_address}</br>
                <b style="color:#26b8f2">COUNTRY</b> : ${param.data.toolTip.country}</br>
                <b style="color:#26b8f2">NAME</b> : ${param.data.toolTip.name}</br>
                <b style="color:#26b8f2">DESC_FULL</b> : ${param.data.toolTip.desc_full}</br>
                <b style="color:#26b8f2">ASN</b> : ${param.data.toolTip.asn}</br>
                <b style="color:#26b8f2">DESC</b> : ${param.data.toolTip.desc}`
            /*
            return `
             <b style="color:#26b8f2">UPDATED</b> : 2017-10-04 03:26:42</br>
             <b style="color:#26b8f2">OWNER_ADDRESS</b> : [u'Information Technology Services Centre', u'', u'The Chinese University of Hong Kong', u'', u'Shatin', u'N.T.', u'Hong Kong']</br>
             <b style="color:#26b8f2">COUNTRY</b> : HK</br>
             <b style="color:#26b8f2">NAME</b> : ERX-CUHKNET</br>
             <b style="color:#26b8f2">DESC_FULL</b> : [u'The Chinese University of Hong Kong', u'ITSC, The Chinese University of Hong Kong', u'Shatin, N.T.,']</br>
             <b style="color:#26b8f2">ASN</b> : 6939</br>
             <b style="color:#26b8f2">DESC</b> : The Chinese University of Hong Kong0`
             */
          }
        },
        animationDurationUpdate: 1500,
        animationEasingUpdate: 'quinticInOut',
        series: [
          {
            type: 'graph',
            layout: 'force',
            // 节点大小
            symbolSize: 100,
            // 画布可以被拖动
            roam: true,
            force: {
              repulsion: 300
            },
            label: {
              normal: {
                show: true,
                fontSize: 12,
                position: 'right',
                color: '#fff'
              }
            },
            lineStyle: {
              normal: {
                type: 'solid',
                color: '#fff',
                width: 1,
                curveness: 0.1
              }
            },
            itemStyle: {
              normal: {
                color: '#26b8f2'
              }
            },
            data: nodes,
            links: links
          }
        ]
      }
    },
    getNodes (id) {
 //     console.log(id)
      /*
      axios.all([
        axios.get('http://192.168.20.30:8000/bgpmap/vis/' + id + '/?token=2017&type=json'),
        axios.get('http://192.168.20.30:8000/bgpmap/asn/' + id + '/?token=2017')
      ]).then(axios.spread(function (vis, asn) {
        console.log(vis.data);
        console.log(asn.data);
      }))
      */
      this.apiGet('http://192.168.20.30:8000/bgpmap/vis/' + id + '/?token=2017&type=json').then(
        (res) => {
          this.nodes = res.nodes;
          this.links = res.links;
          this.nodes.forEach(function (v){
            v['draggable'] = true;
          })
          this.asnChart.setOption(this.asnOption(this.nodes, this.links));
        },
        (err) => {
          console.log(err)
        }
      )
    },
    getToolTip (param) {
      this.apiGet('http://192.168.20.30:8000/bgpmap/asn/' + param.data.name + '/?token=2017').then(
        (res) => {
          param.data['toolTip'] = Object.values(res)[0]
          // this.toolTips = Object.values(res)[0];
          // console.log(tooltip.formatter())
          // this.asnChart.setOption(this.asnOption(this.nodes, this.links));
        },
        (err) => {
          console.log(err)
        }
      )
    }
  },
  created () {
    // this.getNodes(6369);
  },
  mounted () {
    let asnForceChart = document.getElementById('asnForceChart');
    this.asnChart = echarts.init(asnForceChart);
    let self = this;
    this.asnChart.on('click', function (param){
      // console.log(param)
      self.getNodes(param.data.name);
    })
    this.asnChart.on('mouseout', function (param){
      self.getToolTip(param);
      // console.log(param.data);
    })
    this.getNodes(3661);
  },
  mixins: [http]
}
</script>



