import * as types from '../mutation-types'
const state = {
  slideOpen: false
}

// getters
const getters = {
  slideState: state => state.slideOpen
}

// actions
const actions = {
  setSlideState ({ commit, state }) {
    commit(types.SET_SLIDE_STATE)
  },
  closeSlide ({ commit, state }, klose) {
    commit(types.SET_SLIDE_STATE, klose)
  }
}

// mutations
const mutations = {
  [types.SET_SLIDE_STATE] (state, klose) {
    state.slideOpen = typeof klose === 'undefined' ? !state.slideOpen : false
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



